#Close{
    position: absolute;
    height: 2rem;
    border: none;
    color: white;

    -webkit-transform: scale(1.5);

            transform: scale(1.5);
    background-color: #7A000A;

  /*  -webkit-box-shadow: 2px 0px 4px 5px rgba(0,0,0,0.56);
    -moz-box-shadow: 2px 0px 4px 5px rgba(0,0,0,0.56);*/
    box-shadow: -2px 2px 4px 1px rgba(0,0,0,0.56);
}

#Close:hover{
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
    background-color: #CD0011;
}

@-webkit-keyframes UP {
    from {
      opacity: 0;
    }
  
    to {
        opacity: 1;
    }
  }

@keyframes UP {
    from {
      opacity: 0;
    }
  
    to {
        opacity: 1;
    }
  }

