/*phenomena*/

/*Gestión de colores*/
:root{
    --VerdBamboo: #05A535;
    --VerdBambooOscure: #084F1A;
    --MarronBamboo: #80582A;
    --MarronBambooOscure: rgb(68, 46, 29);
    --YellowBamboo: #AA9906;
    --YellowBambooOscure: rgb(95, 88, 33);
    --GrayBamboo: #56555A;
    --GrayBambooOscure: rgb(55, 55, 58);
    --GrayClare: #EFEFEF;
    --GrayOscure: gray;
    --Black: #000000;
    --White: white;
}
