.o-TermAndConditions{
    margin-bottom: 1rem;
}

.o-TermAndConditions label{
    color: var(--VerdBambooOscure);
}

.o-TermAndConditions a{
    color: var(--VerdBambooOscure);
    cursor: pointer;
    text-decoration: underline;
}

.o-TermAndConditions a:hover{
    color: var(--VerdBamboo);
}

.o-FullContainerContact{
    width: 100vw;
    min-height: auto;
    padding: 0rem 0 6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    background-color: var(--White);
    
}

.o-FormContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
}

.o-ContactInformation{
    width: 96vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.o-ContainerText{
    width: 30vw;
    height: 2rem;
    font-family: 'Archivo Narrow', sans-serif;
    border: 0.05rem var(--GrayOscure) solid;
}

.o-ContainerMessage{
    width: 95.8vw;
    height: 10rem;
    margin: 4vh 0;
    font-family: 'Archivo Narrow', sans-serif;
    border: 0.05rem var(--GrayOscure) solid;
}

.o-ButtonSend{
    padding: 0.5rem 6rem;
    border: none;
    background-color: var(--VerdBambooOscure);
    color: var(--White);
    font-family: 'Archivo Narrow', sans-serif;
    box-shadow: 10px 10px 9px -6px rgba(0,0,0,0.51);
}

.o-ButtonSend:hover{
    background-color: var(--VerdBamboo);
}

.o-Localization{
    width: 96%;
    min-height: 50vh;
    margin: 3rem 0;
    display: flex;
    flex-direction: row;
}

.o-Map{
    background-color: var(--VerdBamboo);
    width: 100%;
    height: 50vh;
}

.o-InfoContact{
    height: 50vh;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.o-Group1{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    
}

.o-Info{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.o-Info h3{
    color: var(--Black);
    margin: 0;
}

.o-Info h5{
    color: var(--Black)
}

#h5{
    margin-bottom: 0;
}

#h52{
    margin: 0;
    
}

/*Para pantallas pequeñas*/
@media (max-width: 600px) {

    .o-FormContainer{
        margin-bottom: 4rem;
    } 

    .o-InfoContact{
        flex-direction: column;
    }

    .o-Group1{
        flex-direction: column;
    }

    .o-ContactInformation{
        flex-direction: column;
    }

    .o-ContainerText{
        width: 100%;
        margin: 0.5rem 0;
    }

}